<script>
import { KEYWORD } from "@/constants/index.js";

export default {
  data() {
    return {
      array: [],
    };
  },
  created() {
    if (this.userKeyword) {
      const keys = Object.keys(this.userKeyword);
      keys.forEach((e) => {
        const divByUnderBar = e.split("_");
        if (this.userKeyword[e] === 1)
          this.array.push(KEYWORD[divByUnderBar[1]]);
      });
    }
  },
  methods: {
    toggle(key) {
      this.$emit("toggle", key);
    },
  },
  props: {
    userKeyword: Object,
  },
};
</script>

