<template>
  <div id="mainDiv">
    <h2 class="mainDiv-title">  </h2>
    <input
      type="text"
      class="search-input"
      v-model="keyword"
      placeholder=", ,  "
      @keyup="searchUser"
    />
    <table class="table-striped">
      <thead>
        <th>DB_ID</th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id" @click="goDetail(user.userId)">
          <td>{{ user.id }}</td>
          <td>{{ user.userId }}</td>
          <td>{{ user.password }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.phone }}</td>
          <td>{{ user.role === 1 ? "" : "" }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import http from "@/api/http.js";

export default {
  data() {
    return {
      users: [],
      keyword: "",
    };
  },
  created() {
    this.getAllUsers();
  },
  methods: {
    searchUser() {
      console.log(this.keyword);
      if (this.keyword === "") this.getAllUsers();
      else {
        http.get(`/user/search/${this.keyword}`).then((resp) => {
          this.users = resp.data;
        });
      }
    },
    getAllUsers() {
      http.get(`/user/list/1`).then((resp) => {
        this.users = resp.data;
      });
    },
    goDetail(userId) {
      this.$router.push(`/user/info/${userId}`);
    },
  },
};
</script>

<style scoped src="@/css/user.css"></style>
