<template>
  <div class="page-container">
    <form class="left-container all-center" v-on:submit.prevent="findpw">
      <h1> </h1>
      <input
        type="text"
        class="user-input"
        placeholder=""
        required
        v-model="id"
        ref="id"
      />
      <input
        type="text"
        class="user-input"
        placeholder=""
        required
        v-model="name"
      />
      <input
        type="text"
        class="user-input"
        placeholder=""
        required
        v-model="phone"
      />

      <input type="submit" class="form-btn" value=" " />

      <span class="small-text-container">
        <router-link to="/user/regist" class="small-text"
          > &nbsp;&nbsp;</router-link
        >
        <router-link to="/user/login" class="small-text"
          >&nbsp;&nbsp;</router-link
        >
      </span>
    </form>
    <section class="right-container" v-if="tempPw === ''"></section>
    <section class="right-container-noback" v-if="tempPw !== ''">
          . <br />
      <label>{{ tempPw }}</label>
    </section>
  </div>
</template>

<script>
import http from "@/api/http.js";

export default {
  data() {
    return {
      id: "",
      name: "",
      phone: "",
      tempPw: "",
    };
  },
  methods: {
    findpw() {
      console.log(this.id, this.name, this.phone);
      http
        .post(`/user/findpw/${this.id}/${this.name}/${this.phone}`)
        .then((resp) => {
          if (resp.data !== "fail") {
            alert("  ");
            this.tempPw = resp.data;
          } else {
            alert("  .");
            this.$refs.id.focus();
          }
        });

      return false;
    },
  },
};
</script>

<style scoped src="@/css/user.css"></style>
